<template>
   	<div class="app-container" v-loading="isLoad" >
		<div style="padding: 10px 80px;overflow: hidden;">
			<el-card class="flow-item" :body-style="{ padding: '0px' }" v-for="(item, index) in flows" :key="index">
				<h3 style="font-weight: 500;padding-top:20px;font-size: 24px;color: #666;margin: 0;">套餐{{index+1}}</h3>
				<p class="price">￥{{item.ptPrice}}</p>
				<div class="flow-year">{{item.ptId}}</div>
				<div class="flow-name">{{item.ptName}}</div>
			
			</el-card>
		</div>
	</div>
</template>


<script>
    /* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import {
	getListManufacturerPackage
} from '../../../api/admin/basicData';
export default {
	data() {
		return {
			role: {
				value: '',
				options: [],
			},
			flows: [],
			isLoad: true
		}
	},
	watch: {},
	mounted() {
	this.ListManufacturerPackage(this.cur_mid);
	},
	methods: {
		ListManufacturerPackage() {
			this.isLoad=true
						getListManufacturerPackage()
							.then(res => {
                                this.isLoad=false
								if (res.msg == 0) {
									this.flows = res.packageList;
								}
							})	
		}
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}

.flow-item {
	float: left;
	width: 300px;
	margin-top: 10px;
	text-align: center;
}

.flow-item:nth-child(n+1) {
	margin: 10px 10px 0 10px;
}

.flow-item .price {
	font-size: 40px;
	font-weight: bold;
	color: #666;
	margin: 0;
}

.flow-year {
	color: #aaa;
	font-size: 13px;
}

.flow-name {
	width: 80%;
	margin: 20px auto 15px;
	padding: 15px 0 0 15px;
	border-top: 1px solid #e5ded6;
	text-align: left;
	color: #9ea7b3;
}
</style>



